import { useParams } from "react-router-dom";
import { useState, useEffect } from "react";
import ReactMarkdown from "react-markdown";

export const JedanBlog = () => {
  const { id } = useParams();
  const [blog, setBlog] = useState(null);

  useEffect(() => {
    const loadBlog = () => {
      const context = require.context('../data/blogovi', false, /\.md$/); // Load .md files dynamically
      const blogFile = context(`./${id}.md`); // Load the specific blog file based on id

      // Extract front matter and content using gray-matter
      const { data, content } = require('gray-matter')(blogFile); // Parse the markdown file content

      setBlog({
        ...data,   // Set the metadata (e.g., slika, naslov, datum)
        opis: content // Set the content of the markdown file as 'opis'
      });
    };

    loadBlog();
  }, [id]);

  if (!blog) {
    return <div>Loading...</div>; // Show loading state until blog data is available
  }

  return (
    <div className="px-4 md:pl-[15%] md:pr-[15%] bg-[#F7F8F9] pb-12 w-full">
      <h1 className="text-[#c52233] font-semibold text-3xl md:text-5xl leading-[36px] md:leading-[56px] pb-6">
        {blog.naslov}
      </h1>
      <div className="mx-auto bg-white shadow-lg rounded-lg p-4">
        <div className="flex flex-col md:flex-row gap-4">
          <div className="max-w-full md:max-w-[420px] rounded-lg overflow-hidden flex-shrink-0">
            <img
              src={blog.slika}
              alt={blog.naslov}
              className="object-cover w-full h-full"
            />
          </div>
          <div className="flex flex-col">
            <div className="flex-1">
              <p className="text-gray-700 text-sm md:text-base">
                <ReactMarkdown>{blog.opis}</ReactMarkdown>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
